<template>
  <div>
    <login-app
      :alertInfo="alertInfo"
      :progressLoader="loading"
      :userNotEnabled="userNotEnabled"
      @login="login"
      @loginWithGoogle="loginWithGoogle"
      @sendMail="sendMail"
    />
    <overlay ref="overlay" />
  </div>
</template>

<script>
import Axios from '@/plugins/axios'
import UserMixin from '@/mixins/UserMixin'
import LoginApp from '@/components/auth/Login'
import Overlay from '@/components/globais/overlay/Overlay'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
export default {
  name: 'Login',
  components: { LoginApp, Overlay },
  mixins: [ UserMixin, SnackbarMixin, NavigationMixin ],
  data: () => ({
    alertInfo: null,
    loading: false,
    userNotEnabled: false
  }),
  mounted () {
    this.userNotEnabled = false
  },
  methods: {
    login (data) {
      this.loading = true
      Axios.post('/auth/user/login', data)
      .then(response => {
        this.loading = false
        if (response.data.codigo === 200) {
          let token = response.data.corpo
          this.setUser(token)
        } else this.setError(response.data.corpo)
      })
      .catch ((err) => {
        window.console.log(err)
        this.setError('Não foi possível realizar login na aplicação!')
      })
    },
    loginWithGoogle (data) {
      this.$refs.overlay.showOverlay('Carregando...')
      Axios.post('/auth/user/login-google', data)
      .then(response => {
        if (response.data.codigo === 200) {
          let token = response.data.corpo
          this.setUser(token)
        } else this.setError(response.data.corpo)
      })
      .catch (() => this.setError('Não foi possível realizar login na aplicação!'))
    },
    onSendMailError (msg) {
      this.$refs.overlay.hideOverlayError(msg);
      this.showSnackbarErro(msg);
    },
    sendMail() {
      const sendMailMessage = ''
      this.$refs.overlay.showOverlay('Enviando e-mail...');
      Axios.post('/user/resend-register-mail', {
        email: this.user.email
      }).then((response) => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            this.userNotEnabled = false
            this.alertInfo = false
            this.$refs.overlay.hideOverlaySuccess(response.data.corpo);
          } else this.onSendMailError(response.data.corpo);
        } else this.onSendMailError(sendMailMessage);
      }).catch(() => this.onSendMailError(sendMailMessage));
    },
    setError (msg) {
      this.$refs.overlay.hideOverlay()
      this.alertInfo = null
      this.loading = false
      this.showSnackbarErro(msg)
    },
    setUser (token) {
      let routePath = localStorage.routePathSite
      this.storeUser(token)
      let user = this.getUser()
      if (user) {
        if (user.enabled) {
          if (routePath && routePath !== this.$route.path) {
            localStorage.removeItem('routePathSite')
            this.goToPath(routePath)
          } else this.goTo('home')
        }
        this.userNotEnabled = true;
        this.$refs.overlay.hideOverlay();
        //else this.alertInfo = "Seu cadastro ainda não foi confirmado!!<br>Verifique sua caixa de e-mail e confirme o seu cadastro."
      } else this.setError('Não foi possível recuperar o token de acesso à aplicação!')
    }
  }
}
</script>